<template>
  <div id="Issue">
      <div class="headers">
        <img src="../../assets/copy4.png" >
        若在扫码租车时，提示问题导致无法完成取车，可尝试以下解决方案：
      </div>
    <ul>
      <li  v-for="(item,index) in listArr" :key="index"  @click="navClick(index)" :class="index==listId?'active_li':''"  >
          <div class="liTitle">
             {{item.title}}
            <img src="../../assets/copy5.png" >
          </div>
         <div class="liCenten">
           {{item.text}}
         </div>
      </li>
    </ul>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        listArr:[
          {
           title:"车辆被其它用户绑定",
           text:"答：需联系我们解绑该车辆,用户才能完成取车操作。后续会优化此操作流程。"
          },
          {
            title:"车辆类型不匹配",
            text:"答：商家在进行车辆录入时,车辆录入的商品与用户下单商品不一致,需下架后重新录入该商品。后续会优化此流程。"
          },
          {
            title:"车辆检测不到电池",
            text:"答：用户扫码取车时请确保车辆内已经放入电池;可尝试启动车辆再进行扫码取车。"
          },
          {
            title:"商家套餐不足",
            text:"答：用户取车前,商家换电套餐不得低于用户租赁天数,若不足需完成套餐购买,购买完成后用户即可取车。"
          },
          {
            title:"其它问题",
            text:"答：若扫码取车失败,建议走提车码取车或联系商家更换车辆。"
          }
        ],
        listId:-1
      };
    },
    methods: {
      navClick(index){
        if(index==this.listId){
          this.listId=-1
        }else {
          this.listId=index;
        }
      }
    }
  }
</script>
<style scoped  lang="scss">
  #Issue{
    .headers{
      width: 100%;
      background: #FAF9DA;
      padding: 14px 34px 14px 80px;
      box-sizing: border-box;
      text-align: left;
      color: #FC9153;
      font-size:24px;
      font-family:PingFangSC-Regular;
      font-weight:400;
      line-height:34px;
      position: relative;
      img{
        width:30px;
        height:32px;
        position: absolute;
        left:44px ;
        top: 18px;
      }
    }
  }
  ul{
     width: 100%;
    padding: 0 40px;
    box-sizing: border-box;
     li{
       border-bottom: 1px solid #F0F0F0;
       width: 100%;
       position: relative;
       img{
         position: absolute;
         right: 0px;
         width:48px;
         height:48px;
         top: 32px;
         transform:rotate(0deg);
         transition-duration:200ms;
       }
       .liTitle{
         text-align: left;
           height:110px;
           width: 100%;
            line-height: 110px;
           font-size:28px;
           font-family:PingFangSC-Regular;
           font-weight:400;
           color:rgba(72,72,72,1);
       }
       .liCenten{
         font-size:26px;
         font-family:PingFangSC-Regular;
         font-weight:400;
         color:rgba(153,153,153,1);
         width: 100%;
         margin-top: -22px;
         padding-bottom: 50px;
         text-align: left;
         display: none;
       }
     }
    .active_li{
      .liCenten{
        display: block;
      }
      img{
        transform:rotate(90deg);
        transition-duration:200ms;
      }
    }
  }
</style>





